import { Route } from 'react-router-dom'
import Nav from './Comps/Nav'
import Home from './Comps/Main/Home'
import Airdrop from './Comps/Main/Airdrop'
import Mining from './Comps/Main/Mining'
import Compliance from './Comps/Main/Compliance'
import Voting from './Comps/Main/Voting'
import Tutorial from './Comps/Main/Tutorial'
import ChangeLog from './Comps/Main/ChangeLog/ChangeLog'
import Account from './Comps/Main/Account/Account'

function App() {
  return (
    <div className={'App'}>
      <Nav/>
      <Route
        exact
        path={'/'}
        render={(props) => <Home {...props}/> }/>
      <Route
        path={'/account'}
        render={(props) => <Account {...props}/> }/>
      <Route
        path={'/airdrop'}
        render={(props) => <Airdrop {...props}/> }/>
      <Route
        path={'/mining'}
        render={(props) => <Mining {...props}/> }/>
      <Route
        path={'/governance'}
        render={(props) => <Voting vNadoAmt={'0'} nadoAmt={'0'} {...props}/> }/>
      <Route
        path={'/compliance'}
        render={(props) => <Compliance {...props}/> }/>
      <Route
        path={'/tutorial'}
        render={(props) => <Tutorial {...props}/> }/>
      <Route
        path={'/stats'}
        render={(props) => <div>stats</div> }/>
      <Route
        path={'/changelog'}
        render={(props) => <ChangeLog {...props}/> }/>
    </div>
  )
}

export default App