import React, { Component } from 'react'
import Deposit from './Deposit'
import Withdraw from './Withdraw'
import '../Home.css'

class DepWith extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isDeposit: true,
    }
    this.handleClick = this.handleClick.bind(this)
  }
  handleClick() {
    this.setState({isDeposit: !this.state.isDeposit})
  }
  render() {
    const { token, tokenAmt, amtSelected, tokSelected } = this.props
    const { isDeposit } = this.state
    const DepositWithdraw = (props) => (
      <div
        className={'depWith'}
        onClick={this.handleClick.bind(this, props.txt)}
        style={{
          alignSelf: 'center',
          fontSize: '18px',
          fontWeight: 'bold',
          padding: '2px 4px',
          color: props.color ? '#0033AD' : '#D7DADC',
         }}>
        {props.txt}
      </div>
    )
    return (
      <div style={{ border: '1px solid #d7dadc', borderRadius: '2px', height: '40vh', width: '30vw' }} className={'deposits'}>
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', borderBottom: '1px solid #d7dadc' }}>
          <DepositWithdraw 
            color={isDeposit}
            txt={'Deposit'}/>
          <DepositWithdraw
            color={!isDeposit}
            txt={'Withdraw'}/>
        </div>
        {isDeposit ?
          <Deposit
            token={token}
            tokenAmt={tokenAmt}
            tokSelected={tokSelected}
            amtSelected={amtSelected}/>
          :
          <Withdraw/>
        }
      </div>
    )
  }
}

export default DepWith