import { nanoid } from 'nanoid'
import React, { Component } from 'react'
import { DropdownButton, Dropdown } from 'react-bootstrap'
import Connect from '../../Reuse/Connect'
import RadioBtn from '../../Reuse/RadioBtn'
import '../Home.css'

class Deposit extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
    this.handleTok = this.handleTok.bind(this)
  }

  handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
  }

  render() {
    const { token, tokenAmt, amtSelected } = this.props

    const DropDownCoin = () => (
      <div style={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        placeItems: 'center',
      }}>
        <div style={{ margin: '0 .5vw 0 0' }}>{token}</div>
        <div className={'ddArrow'}></div>
      </div>
    )

    return [
      <div style={{ padding: '0 4px' }} key={nanoid(4)}>Token</div>,
      <DropdownButton
        id={'dd-btn'}
        key={nanoid(4)}
        title={
          <DropDownCoin/>
        }>
        <div className={'tokDropDown'} key={nanoid(4)}>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            ADA
          </Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            DAI
          </Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            cDAI
          </Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            USDC
          </Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            USDT
          </Dropdown.Item>
          <Dropdown.Divider/>
          <Dropdown.Item onClick={this.handleTok} className={'tokName'}>
            WBTC
          </Dropdown.Item>
        </div>
      </DropdownButton>,
      <div style={{ padding: '0 4px' }} key={nanoid(4)}>Amount</div>,
      <div style={{ display: 'flex', justifyContent: 'space-evenly' }} key={nanoid(4)}>
        <RadioBtn
          amt={'0.1'}
          token={token}
          tokenAmt={tokenAmt}
          onClick={amtSelected}/>
        <RadioBtn
          amt={'1'}
          token={token}
          tokenAmt={tokenAmt}
          onClick={amtSelected}/>
        <RadioBtn
          amt={'10'}
          token={token}
          tokenAmt={tokenAmt}
          onClick={amtSelected}/>
        <RadioBtn
          amt={'100'}
          token={token}
          tokenAmt={tokenAmt}
          onClick={amtSelected}/>
      </div>,
      <Connect
        to={'/account'}
        key={nanoid(4)}
        className={'homeConnect'}/>
    ]
  }
}

export default Deposit