import React, { Component } from 'react'

class Compliance extends Component {
  constructor(props) {
    super(props)
    this.state = {
      noteTxt: ''
    }
    this.onChanged = this.onChanged.bind(this)
  }
  onChanged(e) {
    this.setState({ noteTxt: e.target.value })
  }
  render() {
    const { noteTxt } = this.state
    return (
      <div style={{ display: 'flex', flexDirection: 'column', textAlign: 'center', margin: '10vh 15vw' }}>
        <div style={{ fontSize: '25px', margin: '5vh 0' }}>Compliance</div>
        <div>Maintaining financial privacy is essential to preserving our freedoms.</div>
        <div>However, it should not come at the cost of non-compliance. With Cardnado, you can always provide cryptographically verified proof of transactional history using the Ethereum address you used to deposit or withdraw funds. This might be necessary to show the origin of assets held in your withdrawal address.</div>
        <div>To generate a compliance report, please enter your Cardnado Note below.</div>
        <form style={{ margin: '2vh 0', border: '1px solid green', }}>
          <div>Note</div>
          <input
            type={'text'}
            name={'noteInput'}
            value={noteTxt}
            onChange={this.onChanged}
            placeholder={'Please enter your note'}
            style={{
              color: '#d7dadc',
              backgroundColor: '#1a1a1a',
              border: '1px solid #1a1a1a',
              borderRadius: '2px',
              width: '98%'
            }}
          />
        </form>
      </div>
    )
  }
}

export default Compliance