import React, { Component } from 'react'
import Stats from './Stats'
import DepWith from './DepWith/DepWith'
// import { Link } from 'react-router-dom'
import './Home.css'

class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      uCount: `${Math.floor(Math.random() * 2000)}`,
      usrIP: '69.69.69.69',
      tokenAmt: '0.1',
      token: 'ADA',
    }
    this.tokSelected = this.tokSelected.bind(this)
    this.amtSelected = this.amtSelected.bind(this)
  }

  componentDidMount() {
    fetch('https://api.ipify.org?format=json', {
      headers: { 'accept': 'application/json', 'content-type': 'application/json' }
    })
      .then(res => res.json())
      .then(res => this.setState({ usrIP: res.ip }))
      .catch((e) => console.error('Problem fetching my IP', e))
  }

  amtSelected(arg) {
    console.log('amtSelected arg', arg)
    this.setState({tokenAmt: arg})
  }

  async tokSelected(arg) {
    console.log('tokSelected arg', arg)
    this.setState({
      token: arg,
      uCount: `${Math.floor(Math.random() * 2000)}`,
    })

  }

  render() {
    const { usrIP, uCount, tokenAmt, token } = this.state
    return (
      <div className={'homeWithBanner'}>
        <div style={{ 
          justifySelf: 'center',
          alignSelf: 'center',
          border: '1px solid #d7dadc',
          padding: '1vh 1vw',
          borderRadius: '2px'
        }}>
          {/* <span>Cardnado </span> */}
          {/* <Link to={'#'}>was audited</Link> */}
          {/* <span>. However, it is still an experimental software. Please use at your own risk.</span> */}
          <span>Cardnado is in alpha. Please use at your own risk.</span>
        </div>
        <div className={'homePage'}>
          <DepWith
            token={token}
            tokenAmt={tokenAmt}
            tokSelected={this.tokSelected}
            amtSelected={this.amtSelected}/>
          <Stats
            usrIP={usrIP}
            uCount={uCount}
            tokenAmt={tokenAmt}
            token={token}/>
        </div>
      </div>
    )
  }
}

export default Home