import React, { Component } from 'react'
import '../Home.css'

class Withdraw extends Component {
  constructor(props) {
    super(props)
    this.state = {
      note: '',
      recipientAddr: '',
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleDonate = this.handleDonate.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.handleReq = this.handleReq.bind(this)
  }
  handleTok(e) {
    let { innerText } = e.target
    this.props.tokSelected(innerText)
  }
  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value })
  }
  handleDonate() {
    this.setState({ recipientAddr: 'donation_address' })
  }
  handleSubmit(e) {
    e.preventDefault()
    const { note, recipientAddr } = this.state
    if (note.length > 0 && recipientAddr.length > 0) {
      this.handleReq({note: note, recipientAddr: recipientAddr})
    }
  }
  handleReq({...args}) {
    console.log('args', args)
    let { ethereum } = window
    const { networkVersion, selectedAddress } = ethereum
    console.log('Object.keys(ethereum)', Object.keys(ethereum))
  }
  render() {
    const { recipientAddr, note } = this.state
    return (
      <form
        onSubmit={this.handleSubmit}
        style={{
          display: 'flex', 
          flexDirection: 'column',
        }}>
        <div style={{ padding: '0 1vw', }}>Note</div>
        <input
          type={'text'}
          name={'note'}
          value={note}
          onChange={this.handleChange}
          style={{
            backgroundColor: '#1a1a1a',
            borderRadius: '2px',
            border: '1px solid #1a1a1a',
            width: '92%',
            margin: '0 1vw',
            alignSelf: 'center',
          }} />
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '0 1vw', }}>
          <div>Recipient Address</div>
          <div
            style={{ color: '#0033AD', cursor: 'pointer', }}
            onClick={this.handleDonate}>
            Donate
           </div>
        </div>
        <input
          type={'text'}
          name={'recipientAddr'}
          value={recipientAddr}
          onChange={this.handleChange}
          style={{
            backgroundColor: '#1a1a1a',
            borderRadius: '2px',
            border: '1px solid #1a1a1a',
            width: '92%',
            margin: '0 1vw',
            alignSelf: 'center',
          }}/>
        <input
          type={'submit'}
          value={'Withdraw'}
          className={'homeConnect'}
          style={{
            width: '10vw',
            margin: '5vh 0',
            alignSelf: 'center',
            color: '#d7dadc',
            border: '1px solid #0033ad',
            padding: '1vw 1vh',
            textAlign: 'center',
            borderRadius: '2px',
            backgroundColor: 'transparent'
          }}/>
      </form>
    )
  }
}

export default Withdraw